import prevData from './3'

export default prevData.map((item) => {
	if (item.id === 2) {
		return {
				...item,
				position: {
					"lat": 50.081497,
					"lng": 14.414266,
				},
		}
	} else if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.077984,
					"lng": 14.419531,
				},
		}
	} else if (item.id === 4) {
		return {
				...item,
				position: {
					"lat": 50.096763,
					"lng": 14.408018,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.099122,
					"lng": 14.443539,
				},
		}
	} else {
		return item
	}
})



