import prevData from './5'

export default prevData.map((item) => {
	if (item.id === 2) {
		return {
				...item,
				position: {
					"lat": 50.080698,
					"lng": 14.407979,
				},
		}
	} else if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.075753,
					"lng": 14.414896,
				},
		}
	} else if (item.id === 4) {
		return {
				...item,
				position: {
					"lat": 50.092937,
					"lng": 14.406344,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.097305,
					"lng": 14.434698,
				},
		}
	} else {
		return item
	}
})



