import prevData from './4'

export default prevData.map((item) => {
	if (item.id === 2) {
		return {
				...item,
				position: {
					"lat": 50.081290,
					"lng": 14.407722,
				},
		}
	} else if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.075781,
					"lng": 14.419059,
				},
		}
	} else if (item.id === 4) {
		return {
				...item,
				position: {
					"lat": 50.093680,
					"lng": 14.409349,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.098516,
					"lng": 14.439848,
				},
		}
	} else {
		return item
	}
})



