import prevData from './7'

export default prevData.map((item) => {
	if (item.id === 1) {
		return {
				...item,
				position: {
					"lat": 50.075752,
					"lng": 14.420121,
				},
		}
	} else if (item.id === 2) {
		return {
				...item,
				position: {
					"lat": 50.075773,
					"lng": 14.408627,
				},
		}
	} else if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.075395,
					"lng": 14.407558,
				},
		}
	} else if (item.id === 4) {
		return {
				...item,
				position: {
					"lat": 50.089954,
					"lng": 14.410121,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.094442,
					"lng": 14.419592,
				},
		}
	} else {
		return item
	}
})



