import React from 'react'
import { Map, Marker, GoogleApiWrapper } from 'google-maps-react'

interface Position {
	lat: number
	lng: number
}

interface Item {
	id: number
	position: Position
}

export interface MapProps {
	items: Item[]
}

const PRAGUE_LOCATION: Position = {
	lat: 50.0875692,
	lng: 14.4211875,
}

class MapContainer extends React.Component<MapProps, {}> {
	render() {
		const { items } = this.props

		return (
			<Map
				google={this.props.google}
				zoom={14}
				initialCenter={PRAGUE_LOCATION}
			>
				{items.map((item) => (
					<Marker
						key={item.id}
						position={item.position}
						icon={window.google && {
							url: 'https://www.rekola.cz/modules/api/icons/generated/pin-normal-bike-1x.png',
							anchor: new window.google.maps.Point(17, 29),
						}}
					/>
				))}
			</Map>
		)
	}
}

export default GoogleApiWrapper({
	apiKey: ('AIzaSyCUA9KFHim6A5KYK0iRntdDanc4paKmBvg'),
})(MapContainer)



