import prevData from './6'

export default prevData.map((item) => {
	if (item.id === 2) {
		return {
				...item,
				position: {
					"lat": 50.078729,
					"lng": 14.408133,
				},
		}
	} else if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.075395,
					"lng": 14.409446,
				},
		}
	} else if (item.id === 4) {
		return {
				...item,
				position: {
					"lat": 50.091395,
					"lng": 14.409863,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.095433,
					"lng": 14.428432,
				},
		}
	} else {
		return item
	}
})



