import prevData from './10'

export default prevData.map((item) => {
	if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.075022,
					"lng": 14.407627,
				},
		}
	} else if (item.id === 4) {
		return {
				...item,
				position: {
					"lat": 50.089320,
					"lng": 14.410765,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.087338,
					"lng": 14.415043,
				},
		}
	} else {
		return item
	}
})



