import prevData from './2'

export default prevData.map((item) => {
	if (item.id === 2) {
		return {
				...item,
				position: {
					"lat": 50.082185,
					"lng": 14.418601,
				},
		}
	} else if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.081041,
					"lng": 14.419789,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.101599,
					"lng": 14.443367,
				},
		}
	} else {
		return item
	}
})



