import prevData from './9'

export default prevData.map((item) => {
	if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.074967,
					"lng": 14.407766,
				},
		}
	} else if (item.id === 4) {
		return {
				...item,
				position: {
					"lat": 50.088963,
					"lng": 14.410722,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.089761,
					"lng": 14.411610,
				},
		}
	} else {
		return item
	}
})



