import prevData from './1'

export default prevData.map((item) => {
	if (item.id === 2) {
		return {
				...item,
				position: {
					"lat": 50.082846,
					"lng": 14.421219,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.103251,
					"lng": 14.443539,
				},
		}
	} else {
		return item
	}
})



