import React from 'react'

const BROWSER = typeof window !== 'undefined'

const Map = BROWSER ? require('../components/Map') : {}

const data = [
	require('../dummyData/1').default,
	require('../dummyData/2').default,
	require('../dummyData/3').default,
	require('../dummyData/4').default,
	require('../dummyData/5').default,
	require('../dummyData/6').default,
	require('../dummyData/7').default,
	require('../dummyData/8').default,
	require('../dummyData/9').default,
	require('../dummyData/10').default,
	require('../dummyData/11').default,
]

const REFRESH_INTERVAL = 1500

interface State {
	mapProps: any
}

export default class Home extends React.Component<{}, State> {
	private counter: number = 0
	private timer: number

	state: State = {
		mapProps: {
			items: [],
		},
	}

	componentDidMount() {
		if (BROWSER) {
			this.update()
		}
	}

	componentWillUnmount() {
		if (BROWSER) {
			window.clearTimeout(this.timer)
		}
	}

	update = () => {
		this.setState({
			mapProps: {
				items: data[this.counter % data.length],
			}
		})

		this.counter++

		this.timer = window.setTimeout(this.update, REFRESH_INTERVAL)
	}

	render() {
		const { mapProps } = this.state

		return (
			<div style={{
				position: 'fixed',
				top: 0,
				left: 0,
				bottom: 0,
				right: 0,
			}}>
				{Map.default && <Map.default
					{...mapProps}
				/>}
			</div>
		)
	}
}



