import prevData from './8'

export default prevData.map((item) => {
	if (item.id === 2) {
		return {
				...item,
				position: {
					"lat": 50.075470,
					"lng": 14.408176,
				},
		}
	} else if (item.id === 3) {
		return {
				...item,
				position: {
					"lat": 50.074927,
					"lng": 14.407687,
				},
		}
	} else if (item.id === 4) {
		return {
				...item,
				position: {
					"lat": 50.088907,
					"lng": 14.410293,
				},
		}
	} else if (item.id === 5) {
		return {
				...item,
				position: {
					"lat": 50.093395,
					"lng": 14.414270,
				},
		}
	} else {
		return item
	}
})



